.champ_skills {
  display: flex;
  li {
    position: relative;
    margin-right: 10px;
    width: 50px;
    height: 50px;

    .skill_command {
      position: absolute;
      top: 0;
      right: 3px;
    }
    img {
      width: 100%;
    }
    .skill_ex {
      display: block;
    }
  }
}
